@import url(https://fonts.googleapis.com/css2?family=Forum&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  z-index: 1;
}
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: "Forum", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-image: url(https://images.pexels.com/photos/4623060/pexels-photo-4623060.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260
  );
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100vw;
  /* background-size: cover; */
  /* font-family: "Neutra Text", sans-serif; */
  /* transform: translate3d(0px, 0px, 0px); */
  /* transition: all 700ms ease; */
}
main{
  background: white;
}
code {
  font-family: 'Forum', cursive;
}

.mainBlock h1 {
  font-family: "Forum", cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 3.8rem;
  line-height: 4.2rem;
  text-align: center;
  letter-spacing: 0.035em;
  color: #db8e8e;
  margin-bottom: 5%;
}

.mainBlock p {
  /* font-family: Neutra Text; */
  font-family: "Forum", cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.7rem;
  text-align: center;
  letter-spacing: 0.23em;

  color: #413131;
}

#start-slider {
  height: 500px;
  width: 800px;
}

.response {
  margin-left: -25% !important;
}

.response h1 {
  margin-bottom: 20px;
  font-style: 60px;
  font-style: italic;
}

@media (max-width: 800px) {
  #start {
    height: -webkit-max-content;
    height: max-content;
  }
  #start-slider {
    height: 300px;
    width: 100vw;
  }

  .mainBlock h1,
  .response h1 {
    font-size: 35px;
    line-height: 3.2rem;
  }
  .mainBlock p, .response p {
    font-size: 14px;
  }
}
@media (max-width: 500px) {
  .mainBlock h1 {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }
  .mainBlock p {
    font-size: 8px;
    line-height: 1rem;
  }
}
.decor {
  font-size: 5vw;
  letter-spacing: 1em;
  text-align: center;
  color: rgba(218, 187, 187, 0.904);
}

.gallery {
  -webkit-column-count: 3;
          column-count: 3;
  -webkit-column-width: 33%;
          column-width: 33%;
  padding: 0 15%;
}
@media (max-width: 800px) {
  .gallery {
    -webkit-column-count: 2;
    column-count: 2;
    -webkit-column-width: 50%;
            column-width: 50%;
  }
}
@media (max-width: 500px) {
  .gallery {
    -webkit-column-count: 1;
    column-count: 1;
    -webkit-column-width: 100%;
            column-width: 100%;
  }
}

.gallery > img {
  width: 100%;
  max-width: 350px;
  /* height: 300px; */
  aspect-ratio: 3/4;
  object-fit: cover;
  transition: all 350ms ease;
  margin-bottom: 30px;
}

.row {
  justify-content: center;
  align-items: center;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-wrapper {
  background: transparent;
}

.swiper h1,
.swiper p {
  color: white;
  text-align: start;
}

.App {
  text-align: center;
  /* font-family: "Neutra Text", sans-serif;
   */
  font-family: "Forum", cursive;
}

.logo {
  /* max-width: 100px; */
  min-width: 40px;
  width: 100%;
  flex-grow: 1;
  margin-top: 5%;
}

footer {
  display: flex;
  height: -webkit-max-content;
  height: max-content;
  background:#e3e3e3;
  flex-direction: column;
  justify-content: start;
  padding: 0 5%;
  align-items: space-between;
  width: 100vw;
  margin-bottom: 5%;
}

footer .logo {
  height: 80px;
}
.footer-nav > a {
  font-size: 16px !important;
}

.copyright {
  margin: 10px 0 0;
  font-size: small;
}
@media (max-width: 750px) {
  .footer-nav > a {
    font-size: 12px !important;
  }
  .footer-soc svg {
    width: 18px;
    height: 18px;
  }
  .copyright {
    font-size: xx-small;
  }
}

main {
  min-height: 65vh;
}

@media (max-width: 800px) {
  #relative-navbar-nav {
  display: none;
  }
}

.navbar-toggler:focus {
  border: transparent;
}

.navbar-light .navbar-toggler {
  color: transparent;
  border-color: transparent;
}

.navbar-collapse {
  width: 100%;
  justify-content: space-between;
}

#basic-navbar-nav.show,
#basic-navbar-nav.collapsing {
  display: none;
}

.navbar-toggler {
  font-size: 1.3rem;
}
.navbar-light .navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba(190, 110, 110, 0.7)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

@media (max-width: 600px) {
  .navbar-toggler {
    font-size: 1rem;
  }
}

